<template>
  <div class="discovery-container">
    <!--  -->
    <div class="banner-pc">
      <!--  -->
      <el-carousel :interval="4000" type="card" height="200px">
        <el-carousel-item v-for="(item, index) in banners" :key="index">
          <!-- , -->
          <img v-lazy="item.imageUrl" alt="" @click="PlayBanner(item)" />
          <h3 class="medium">{{ item }}</h3>
        </el-carousel-item>
      </el-carousel>
    </div>

    <!--  -->
    <div class="banner-phone">
      <el-carousel indicator-position="outside">
        <el-carousel-item v-for="(item, index) in banners" :key="index">
          <!-- , -->
          <img v-lazy="item.imageUrl" alt="" @click="PlayBanner(item)" />
          <h3 class="medium">{{ item }}</h3>
        </el-carousel-item>
      </el-carousel>
    </div>

    <!--  -->
    <div class="recommend">
      <h3 class="title"></h3>
      <!--  -->
      <div class="items">
        <div class="item" v-for="(item, index) in list" :key="index">
          <div class="img-wrap">
            <!--  -->
            <div class="desc-wrap">
              <span class="desc">{{ item.playCount | ellipsisPlayVolume }}</span>
            </div>
            <!--  -->
            <img v-lazy="item.picUrl" alt="" @click="toPlaylist(item.id)" />
            <span class="iconfont icon-play" @click="toPlaylist(item.id)"></span>
          </div>
          <!--  -->
          <p class="name">{{ item.name }} </p>
        </div>
      </div>
    </div>

    <!--  -->
    <div class="news">
      <h3 class="title"></h3>
      <div class="items">
        <div class="item" v-for="(item, index) in songs" :key="index" @dblclick="PlayMusic(item)">
          <div class="img-wrap">
            <!--  -->
            <img v-lazy="item.picUrl" alt="">
            <span @click="PlayMusic(item)" class="iconfont icon-play"></span>
          </div>

          <div class="song-wrap">
            <!--  -->
            <div class="song-name">{{ item.name }}</div>
            <!--  -->
            <div class="singer">{{ item.song.artists[0].name }}</div>
          </div>
        </div>
      </div>
    </div>

    <!-- MV -->
    <div class="mvs">
      <h3 class="title">MV</h3>
      <div class="items">
        <div class="item" v-for="(item, index) in mvs" :key="index" @click="toMvDetail(item.id)">
          <!--  -->
          <div class="img-wrap">
            <img v-lazy="item.picUrl" alt="">
            <!--  -->
            <span class="iconfont icon-play icon1"></span>
            <!--  -->
            <div class="num-wrap">
              <span class="iconfont icon-play icon2"></span>
              <div class="num">{{ item.playCount }}</div>
            </div>
          </div>
          <!-- MV -->
          <div class="info-wrap">
            <!-- MV -->
            <div class="name">{{ item.name }}</div>
            <!--  -->
            <div class="singer">{{ item.artistName }}</div>
          </div>
        </div>
      </div>
    </div>


  </div>
</template>

<script>
import { bannerAPI, recommendSonglistAPI, recommendSongAPI, recommendMVAPI, checkMusicAPI, getSongUrlAPI, songInfoAPI } from '@/utils/api';

export default {
  name: 'discovery',
  data() {
    return {
      //
      banners: [],
      //
      list: [],
      //
      songs: [],
      //MV
      mvs: [],
      // 
      newSongLimit: 15,
    }
  },
  // created()watch
  created() {
    //console.log('created')
    //
    bannerAPI().then(res => {
      // console.log(res)
      this.banners = res.data.banners
    }).then(() => {
      this.loading = false
    })

    // params{limit:15}
    recommendSonglistAPI({ limit: 15 }).then(res => {
      // console.log("",res)
      this.list = res.data.result
    })

    // 
    recommendSongAPI(this.newSongLimit).then(res => {
      // console.log('',res)
      this.songs = res.data.result
    })

    //MV
    recommendMVAPI().then(res => {
      // console.log('MV',res)
      this.mvs = res.data.result
    })

  },

  //
  methods: {

    // 
    PlayMusic(song) {
      // 
      checkMusicAPI(song.id)
        .then(res => {
          // url
          getSongUrlAPI({id : song.id}).then(res => {
            this.$store.dispatch("saveSongUrl", res.data.data[0].url);
          });
          const params = { ids:song.id }
          songInfoAPI(params).then(res => {
            // console.log('detail', res)
            // 
            this.$store.dispatch("changePlayState", true);
            //vuex
            this.$store.dispatch("saveSongDetail", res.data.songs[0]);
            // vuex
            this.$store.dispatch("addPlayinglist", res.data.songs[0]);
            // console.log('nowSongDetail',this.nowSongDetail)
          })
        })
        .catch(err => {
          console.log(err);
          this.$message({
            message: "",
            type: "warning",
            center: true,
          });
        });
    },

    PlayBanner(item) {
      if (item.targetId == 0) return;
      if (item.targetType == 1 || item.typeTitle == 300) {
        // url
        getSongUrlAPI({id: item.targetId}).then(res => {
          this.$store.dispatch("saveSongUrl", res.data.data[0].url);
        }).catch(err => err);
        /* id*/
        const params = { ids : item.targetId }
        songInfoAPI(params).then(res => {
          // vuex
          this.$store.dispatch("saveSongDetail", res.data.songs[0]);
          // vuex
          this.$store.dispatch("addPlayinglist", res.data.songs[0]);
        }).catch(err => err);
      } else if (item.targetType == 1000) {
				return ;
			} else if (item.targetType == 10) {
				return ;
			}
    },

    // 
    toPlaylist(id) {
      // 
      this.$router.push(`/playlist?q=${id}`)
    },
    // MV
    toMvDetail(id) {
      this.$router.push(`/mv?q=${id}`);
    },

  },


}
</script>

<style lang="less" scoped>
.discovery-container {
    position: relative;
    width: 100%;
    height: 100%;
    max-width: 1100px;
    margin: 35px 100px;

    .banner-phone{
      display: none;
    }

    /*  */
    .el-carousel__container {
        height: 230px;
    }

    /*  */
    .el-carousel__item img {
      width: 100%;
      height: 100%;
      border-radius: 10px;
    }

    .title {
      font-weight: normal;
      margin-bottom: 20px;
      padding-left: 8px;
    }

    /*  */
    .recommend {
      margin-bottom: 40px;

      /*  flex */
      .items {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;

        /* overflow hidden */
        .item {
          width: 200px;
          margin: 10px;
          position: relative;
          overflow: hidden;

          /*  */
          img {
            width: 200px;
            height: 200px;
            border-radius: 5px;
          }

          /* '' ...*/
          .desc-wrap {
            position: absolute;
            width: 100%;
            height: 30px;
            left: 0;
            font-size: 16px;
            color: white;
            background: rgba(0, 0, 0, 0.5);
            max-height: 50px; 
            padding: 5px;
            overflow: hidden; /*  */
            text-overflow: ellipsis;  /*  */
            display: -webkit-box;
            -webkit-box-orient: vertical; /*  */
            -webkit-line-clamp: 2;  /*  */
            top: -30px;

            span{
              font-size: 14px;
            }
          }

          &:hover{
            .desc-wrap{
              top: 0;
            }
          }

          .img-wrap{
            position: relative;
            cursor: pointer;

            .icon-play{
              position: absolute;  /*  */
              top: 40%;
              right: 40%;
              width: 40px;
              height: 40px;
              color: #dd6d60;
              font-size: 20px;
              border-radius: 50%;
              display: flex; /*  */
              align-items: center;   /*  */
              justify-content: center; /*  */
              padding-left: 5px;
              background: rgba(255, 255, 255, 0.8);
              opacity: 0; /* 0 */ 
            }

            &:hover{
              .icon-play{
                opacity: 1;
              }
            }
          }

          .name{
            overflow: hidden;   /**/
            text-overflow: ellipsis; /* ellipsis*/
            display: -webkit-box;        /*  */
            -webkit-box-orient: vertical;  /*  */
            -webkit-line-clamp: 1;   /*  */
            font-size: 14px;
          }
        }
      }
    }

    /*  */
    .news{
      margin-bottom: 40px;

      .items{
        height: 500px;
        width: 75%;
        display: flex; /*  */
        flex-direction: column; /*   */
        flex-wrap: wrap; /*  */
        padding-left: 15px; /* 15px */

        .item {
          height: 100px; /*  */
          width: 45%; /*  */
          display: flex;
          align-items: center;
          cursor: pointer; /*  */

          &:hover{
            background-color: #f5f5f5;
          }

          .img-wrap{
            position: relative;
            display: flex;
            align-items: center;
            justify-content: center; /*  */

            img{
              width: 80px;
              height: 80px;
            }

            .iconfont{
              position: absolute; 
              top: 50%;
              left: 50%;
              transform: translate(-50%, -50%); /*  */
              width: 30px;
              height: 30px;
              color: #dd6d60;
              text-align: center;
              line-height: 30px;
              border-radius: 50%;
              font-size: 14px;
              padding-left: 2px;
              background: rgba(255, 255, 255, 0.8);
              opacity: 0; /* 0 */

              &::before{
                transform: translateX(3px);
              }
            }

            &:hover{
              .iconfont{
                opacity: 1; 
              }
            }
          }

          .song-wrap{
            display: flex;
            flex-direction: column; /*   */
            justify-content: space-around; /*  <div>  */
            height: 100%;
            padding: 10px;
            font-size: 16px;

            .song-name{
              overflow: hidden;   /**/
              text-overflow: ellipsis; /* ellipsis*/
              display: -webkit-box;        /*  */
              -webkit-box-orient: vertical;  /*  */
              -webkit-line-clamp: 1;   /*  */
            }

            .singer{
              font-size: 14px;
              color: darkcyan;
              overflow: hidden;   /**/
              text-overflow: ellipsis; /* ellipsis*/
              display: -webkit-box;        /*  */
              -webkit-line-clamp: 1;   /*  */
              -webkit-box-orient: vertical;  /*  */
            }
          }
        }
      }
    }

    .mvs{
      padding-bottom: 80px;

      .items{
        display: flex;
        flex-wrap: wrap;
        /*  <div>  */
        justify-content: space-around;

        .item{
          width: 250px;
          height: 250px;
          cursor: pointer;
          margin-right: 25px;

          .img-wrap{
            width: 250px;
            height: 180px;
            position: relative;

            img{
              width: 100%;
              height: 100%;
              border-radius: 5px;
              box-shadow: 0 0 20px 0px rgba(0, 0, 0, .3);
            }

            .icon1{
              position: absolute;
              top: 50%;
              left: 50%;
              transform: translate(-50%, -50%);
              width: 40px;
              height: 40px;
              color: #ee0000;
              font-size: 20px;
              border-radius: 50%;
              display: flex;
              align-items: center;
              justify-content: center;
              padding-left: 4px;
              padding-bottom: 1px;
              background: rgba(255, 255, 255, 0.8);
              opacity: 0;
            }

            &:hover{
              .icon1{
                opacity: 1;
              }
            }

            .num-wrap {
              position: absolute;
              color: white;
              top: 0;  /*  */
              right: 0;  /*  */
              display: flex;
              align-content: center;
              font-size: 15px;
              padding-right: 5px;
              padding-top: 2px;

              .icon2{
                padding-top: 4px;
                margin-right: 5px;
              }

              .num{
                margin-top: 2px;
              }
            }


          }

          .info-wrap{
            .name{
              font-size: 15px;
              margin: 5px auto;
            }

            .singer{
              font-size: 14px;
              /* color: #c5c5c5; */
              color: darkcyan;
            }
          }
        }
      }
    }
}

/*  */
@media screen and (max-width : 500px) {
  .discovery-container{
    margin: 20px 0 0 0;
  }

  /*  */
  .discovery-container .title{
    text-align: center;
    margin-bottom: 10px;
  }

  /*  */
  .discovery-container .recommend .items .item {
    width: 110px;
    margin: 10px;
    position: relative;
    overflow: hidden;
  }

  /*  */
  .discovery-container .recommend .items .item img {
    width: 100%;
    height: 110px;
    border-radius: 5px;
  }

  /*  */
  .discovery-container .recommend .items .item .name{
    /* height: 40px; */
    -webkit-line-clamp: 2;
    font-size: 10px;
  }

  /* UI */
  .discovery-container .recommend .items .item .desc-wrap span{
    font-size: 4px;
    top: 2px;
    position: absolute;
  }

  /* UI */
  .discovery-container .news .items{
    height: 100%;
    width: 100%;
  }

  /* UI */
  .discovery-container .news .items .item{
    width: 100%;
  }

  /* mvUI */
  .discovery-container .mvs .items .item{
    width: 160px;
    height: 200px;
    margin: 0;
  }

  /*  */
  .discovery-container .banner-phone{
    display: block;
  }

  /* PC */
  .discovery-container .banner-pc{
    display: none;
  }

  /*  */
  .el-carousel__container {
    height: 150px;
  }

  /* MV */
  .discovery-container .mvs .items .item .img-wrap{
    width: 150px;
    height: 120px;
  }

  /* UI */
  .discovery-container .recommend .items .item .img-wrap .icon-play {
    position: absolute; 
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%); /*  */
    width: 30px;
    height: 30px;
    color: #dd6d60;
    text-align: center;
    line-height: 30px;
    border-radius: 50%;
    font-size: 14px;
    background: rgba(255, 255, 255, 0.8);
    opacity: 0; /* 0 */
  } 

  /*  */
  .discovery-container .recommend .items .item .desc-wrap {
    position: absolute;
    width: 100%;
    height: 20px;
    left: 0;
    font-size: 16px;
    color: white;
    background: rgba(0, 0, 0, 0.5);
    max-height: 50px; 
    padding: 5px;
    overflow: hidden; /*  */
    text-overflow: ellipsis;  /*  */
    display: -webkit-box;
    -webkit-box-orient: vertical; /*  */
    -webkit-line-clamp: 1;  /*  */
    top: -30px;
  }

  .discovery-container .mvs .items .item .info-wrap .name{
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
    font-size: 12px;
  }

  .discovery-container .mvs .items .item .info-wrap .singer{
    font-size: 12px;
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
  }

}


@media screen and (max-width : 450px){
  /*  */
  .discovery-container .recommend .items .item {
    width: 90px;
    margin: 10px;
    position: relative;
    overflow: hidden;
  }

  .discovery-container .recommend .items .item img {
    width: 100%;
    height: 90px;
    border-radius: 5px;
  }
}

@media screen and (max-width : 360px){
  /*  */
  .discovery-container .recommend .items .item {
    width: 120px;
    margin: 10px;
    position: relative;
    overflow: hidden;
  }

  .discovery-container .recommend .items .item img {
    width: 100%;
    height: 120px;
    border-radius: 5px;
  }
}

</style>